<template>
  <el-main>
    <div>
      <h2>账号安排随机</h2>
      <div class="param">
        <div class="item">
          <span>总账户数:</span>
          <el-input v-model="totalWallet" placeholder="请输入内容"></el-input>
        </div>
        <div class="item">
          <span>每次任务账户数:</span>
          <el-input v-model="onceWallet" placeholder="请输入内容"></el-input>
        </div>
        <div class="item">
          <span>每几天做一轮:</span>
          <el-input v-model="allDays" placeholder="请输入内容"></el-input>
        </div>
      </div>
      <div class="desc">示例：有40个账号，每天做8个号，计划每周做1遍(40个号过一遍)，需要随机抽5天，每天随机抽8个号，过了1周后，再次随机排序。</div>
      <div style="text-align: center">
        <el-button type="primary" @click="random">生成计划</el-button>
      </div>
      <div v-html="result" class="result"></div>
    </div>
    <div style="margin-top: 100px">
      <h2>任务顺序随机</h2>
      <div class="param">
        <div style="width: 47%">
          <span>任务数:</span>
          <el-input v-model="dapps" placeholder="请输入内容"></el-input>
        </div>
        <div style="width: 47%">
          <span>账户数:</span>
          <el-input v-model="account" placeholder="请输入内容"></el-input>
        </div>
      </div>
      <div class="desc">示例：有10个账户，做一组5个dapp任务ABCDE，需要生产10个随机的ABCDE的顺序。</div>
      <div style="text-align: center">
        <el-button type="primary" @click="random2">生成乱序</el-button>
      </div>
      <div v-html="result2" class="result"></div>
    </div>
  </el-main>
</template>

<script>
import { random, random2 } from "../../../utils/random";

export default {
  name: "Random",
  data () {
    return {
      totalWallet: 40,
      onceWallet: 8,
      allDays: 7,
      result: "",
      dapps: 5,
      account: 10,
      result2: ""
    }
  },
  mounted() {
    this.random();
    this.random2();
  },
  methods: {
    random() {
      this.result = random(this.totalWallet, this.onceWallet, this.allDays);
    },
    random2() {
      this.result2 = random2(this.dapps, this.account);
    }
  }
}
</script>

<style lang="stylus" scoped>
.el-main
  width 800px
  margin 0 auto
  h2
    text-align center

.param
  display flex
  justify-content space-between
  padding 0 30px
  margin-bottom 5px
  .item
    width 30%
    span
      font-size 14px
    .el-input
      margin-top 10px

.desc
  padding 0 30px
  margin-bottom 20px
  font-size 14px
  color #3a8ee6

.result
  margin-top 20px
  padding 15px
  white-space pre-wrap
  font-size 14px
  color gray
  border #3a8ee6 solid 2px
  border-radius 6px
  line-height 1.5


</style>



