/*
 * 随机化生成空投做任务安排，避免女巫行为一致性
*/

export function random (totalWallet, onceWallet, allDays) {
  // 总账号40个，每次做8个账号，每7天做一遍
  // let totalWallet = 40;                   // 总账户数，账号默认编号1-40
  // let onceWallet = 8;                     // 每次任务账户数
  // let allDays = 5;                        // 每7天做一轮

  // 随机化选择，一轮中的哪几天做任务
  let taskDays = Math.round(totalWallet / onceWallet);
  if (allDays < taskDays) {
    // throw new Error("任务不可能完成");
    return "任务不可能完成";
  }
  let yourDays = [];          // 你的任务日，在时间中的第几天
  for (let i = 0; i < taskDays; i++) {
    let num = Math.floor(Math.random() * allDays + 1);
    while (yourDays.indexOf(num)>=0) {
      num = Math.floor(Math.random() * allDays + 1);
    }
    yourDays.push(num);
  }

  // 任务日排序
  for (let i = 0; i < yourDays.length - 1; i++) {
    for (let j = 0; j < yourDays.length - 1 - i; j++) {
      if (yourDays[j] > yourDays[j + 1]) {
        let temp = yourDays[j];
        yourDays[j] = yourDays[j + 1];
        yourDays[j + 1] = temp;
      }
    }
  }

  // 随机分配钱包到任务日
  let yourWallet = [];
  for (let i = 0; i < totalWallet; i++) {
    let num = Math.floor(Math.random() * totalWallet + 1);
    while (yourWallet.indexOf(num) >= 0) {
      num = Math.floor(Math.random() * totalWallet + 1);
    }
    yourWallet.push(num);
  }

  let result = [];
  for (let i = 0; i < yourDays.length; i++) {
    result[i] = [];
    for (let j = 0; j < onceWallet; j++) {
      if (i*onceWallet+j < yourWallet.length) {
        result[i].push(yourWallet[i*onceWallet+j]);
      }
    }
  }

  // 对账户进行排序
  for (let i = 0; i < result.length; i++) {
    for (let j = 0; j < result[i].length; j++) {
      for (let k = 0; k < result[i].length - 1 - j; k++) {
        if (result[i][k] > result[i][k + 1]) {
          let temp = result[i][k];
          result[i][k] = result[i][k + 1];
          result[i][k + 1] = temp;
        }
      }
    }
  }

  let ret = "";
  // 输出结果
  for (let i = 0; i < result.length; i++) {
    let str = "第" + (yourDays[i]) + "天：";
    for (let j = 0; j < result[i].length; j++) {
      str += "账户" + result[i][j] + "\t";
    }
    ret += str + "\n";
    // console.log(str);
  }
  return ret;
}

/*
 * 随机化生成空投做任务安排，避免女巫行为一致性
*/

export function random2 (dapps, acount) {
  let ret = "";
  for (let j = 0; j < acount; j++) {
    let str = "账户" + (j+1) + "：";
    let arr = [];
    for (let i = 0; i < dapps; i++) {
      let num = Math.floor(Math.random() * dapps + 1);
      while (arr.indexOf(num)>=0) {
        num = Math.floor(Math.random() * dapps + 1);
      }
      arr.push(num);
      str += num + "     ";
    }
    ret += str + "\n";
  }
  return ret;
}



